import React, { Component } from "react";
import Skeleton from "react-loading-skeleton";
import { connect } from "react-redux";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Field, reduxForm } from "redux-form";
// import { getNokirim } from "../../../actions/databarang_action";
import { getDataBaki, getDataGudang, hideModal } from "../../../actions/datamaster_action";
import { ReanderSelect } from "../../../components/helpers/field";
// import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  let data_terima_barang =
  localStorage.getItem("data_terima_barang") === null || undefined
    ? []
    : JSON.parse(localStorage.getItem("data_terima_barang") || []);
    
  return {
    initialValues: {
      no_kirim: data_terima_barang.no_kirim || "",
    },
    datagudang: state.datamaster.getDataGudang,
    databaki: state.datamaster.getDataBaki,
  };
};
class FormTerimaBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
        no_kirim: [],
        datagudang:[],
        databaki: [],
        loading:false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataGudang())
    this.props.dispatch(getDataBaki())
    // this.props.dispatch(getNokirim())
  }

  render() {
    return (
      <Modal
        backdrop="static"
        keyboard={false}
        isOpen={this.props.isOpen}
        toggle={() => this.props.dispatch(hideModal())}
      >
        <ModalHeader> {this.props.title} </ModalHeader>
        <form onSubmit={this.props.handleSubmit} autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <ModalBody>
            <div className="form-group">
                <Field
                  label="No Kirim"
                  name="no_kirim"
                  options={this.props.no_kirim
                    .map((list) => {
                    let data = {
                      value: list._id.no_kirim,
                      name: list._id.no_kirim,
                    };
                    return data;
                  })}
                  placeholder={"Silahkan Pilih No Kirim"}
                  component={ReanderSelect}
                />
            </div>
            <div className="form-group">
              {this.state.datagudang !== [] ? (
                <Field
                  label="Kode Gudang"
                  name="kode_gudang"
                  onChange={(e) => {
                    this.setState({ kode_gudang: e })
                    this.props.change('kode_baki','')
                  }}
                  options={this.props.datagudang
                    .filter((list) => list.kode_gudang !== undefined)
                    .map((list) => {
                      let data = {
                        value: list.kode_gudang,
                        name: list.kode_gudang + " - " + list.nama_gudang,
                      };
                      return data;
                    })}
                  placeholder={"Silahkan Pilih Kode Gudang"}
                  component={ReanderSelect}
                />
              ) : (
                <Skeleton width={"100%"} height={50} />
              )}
            </div>
            <div className="form-group">
                <Field
                  label="Kode Baki"
                  name="kode_baki"
                  options={this.props.databaki
                    .filter((list) => list.kode_gudang === this.state.kode_gudang)
                    .map((databaki) => {
                    let data = {
                      value: databaki.kode_baki,
                      name: databaki.kode_baki + " - " + databaki.nama_baki,
                    };
                    return data || [];
                  })}
                  placeholder={"Silahkan Pilih Kode Baki"}
                  component={ReanderSelect}
                />
            </div>
          </ModalBody>
          <ModalFooter>
          <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Cek Data Barang
                </>
              ) : (
                "Cek Data Barang"
              )}
            </button>
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            
          </ModalFooter>
           {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
        </form>
      </Modal>
    );
  }
}

FormTerimaBarang = reduxForm({
  form: "FormTerimaBarang",
  enableReinitialize: true,
})(FormTerimaBarang);
export default connect(maptostate,null)(FormTerimaBarang);
