import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import {
  getNokirim,
  ShowModalTerimaBarang,
} from "../../../actions/databarang_action.jsx";

import { connect } from "react-redux";
import FormTerimaBarang from "./FormTerimaBarang.jsx";
import { getData, postData } from "../../../config/axios.jsx";
import Skeleton from "react-loading-skeleton";
import {
  NotifError,
  NotifSucces,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { PageSettings } from "../../../config/page-settings.js";
// import LoadingButton from "../../../components/helpers/function.jsx";
const { SearchBar } = Search;
const maptostate = (state) => {
  return {
    Nokirim: state.databarang.getNokirim,
    hideModal: state.datamaster.modalDialog,
  };
};

class TerimaBarang extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      isLoadingBtn: false,
      DataTerimaBarang: [],
      SimpanDataBarang: false,
      resetTerimaBarang: false,
      columns: [
        {
          dataField: "no",
          text: "No ",
          formatter: (cell, row, num, index) => {
            this.setState({ num: 1 + num });
            return <div> {++num} </div>;
          },
          footerAttrs: {
            colSpan: "4",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>Total Barang : {this.state.DataTerimaBarang.length} </div>
            );
          },
        },
        {
          dataField: "kode_barang",
          text: "Kode Barang",
          sort: true,
        },
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
        },
        {
          dataField: "kode_intern",
          text: "Kode Intern",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(3),
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
          footer: "",
        },
        {
          dataField: "berat_atribut",
          text: "Berat Atribut",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(3),
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getNokirim());
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
  }
  resetTerimaBarang() {
    this.setState({
      resetTerimaBarang: true,
    });
    setTimeout(() => {
      this.setState({
        DataTerimaBarang: [],
        resetTerimaBarang: false,
      });
    }, 300);
  }
  handleSubmit(data) {
    let data_post = {
      no_kirim: data.no_kirim,
      kode_gudang: data.kode_gudang,
      kode_toko: data.kode_baki,
    };
    localStorage.setItem("data_terima_barang", JSON.stringify(data_post));
    this.setState({ SimpanDataBarang: data_post });
    this.setState({ isLoading: true });
    let data_edit = {
      no_kirim: data.no_kirim,
    };
    getData("kirimbarang/get/nokirim/" + data.no_kirim, data_edit)
      .then((res) => {
        this.setState({
          DataTerimaBarang: res.data,
        });
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .then(() => {
        this.props.dispatch(reset("FormTerimaBarang"));
      })
      .then(() => {
        this.props.dispatch(hideModal());
      });
  }

  formEditBaki(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTerimaBarang(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalTerimaBarang() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTerimaBarang(false));
    this.setState({
      isEdit: false,
    });
  }
  terimabangOnline() {
    this.setState({ isLoadingBtn: true });
    postData("terimabarang/add", this.state.SimpanDataBarang)
      .then(() => {
        this.setState({
          DataTerimaBarang: [],
        });
      })
      .then(() => {
        this.props.dispatch(reset("FormTerimaBarang"));
        localStorage.setItem("data_terima_barang", JSON.stringify([]));
      })
      .then(() => {
        this.props.dispatch(getNokirim());
      })
      .then(() => {
        this.setState({ isLoadingBtn: false });
      })
      .then(() => {
        NotifSucces("Barang Berhasil Diterima");
      })
      .catch((error) => this.errorHandling(error));
  }
  errorHandling(err) {
    NotifError(err);
    this.setState({ isLoadingBtn: false });
  }
  render() {
    return (
      <div>
        <Link to="/home">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <Panel>
          <PanelHeader>Terima Barang</PanelHeader>
          <br />

          {/* Terima Barang */}
          <div className="container">
            {this.state.isLoading === false ? (
              <ToolkitProvider
                keyField="kode_barang"
                data={this.state.DataTerimaBarang || []}
                columns={this.state.columns}
                search
                exportCSV={{
                  fileName: "Export Terima Barang.csv",
                }}
              >
                {(props) => (
                  <div className="row">
                    <div className="col-6">
                      <button
                        onClick={() => this.ShowModalTerimaBarang()}
                        className="btn btn-teal"
                      >
                        {" "}
                        <i className="fa fa-plus"></i>{" "}
                      </button>
                    </div>
                    <div className="col-6">
                      <div className="text-right">
                        <SearchBar {...props.searchProps} />
                      </div>
                    </div>
                    <hr />
                    <div className="col-12">
                      <BootstrapTable {...props.baseProps} />
                      <br />

                      <>
                        <button
                          className="btn btn-primary"
                          onClick={() => this.terimabangOnline()}
                        >
                          {/* {this.state.isLoadingBtn ? "" : "Terima Barang"} */}
                          {this.state.isLoadingBtn ? (
                            <>
                              <i className="fas fa-spinner fa-spin"></i> &nbsp;
                              Terima Barang
                            </>
                          ) : (
                            "Terima Barang"
                          )}
                        </button>
                        &nbsp;
                        <button
                          className="btn btn-warning"
                          onClick={() => this.resetTerimaBarang()}
                        >
                          {this.state.resetTerimaBarang ? (
                            <>
                              <i className="fas fa-spinner fa-spin"></i> &nbsp;
                              Reset
                            </>
                          ) : (
                            "Reset"
                          )}
                        </button>
                      </>
                    </div>
                  </div>
                )}
              </ToolkitProvider>
            ) : (
              <Skeleton width={"100%"} height={200} />
            )}
            <FormTerimaBarang
              no_kirim={this.props.Nokirim}
              isLoading={this.state.isLoading}
              isEdit={this.state.isEdit}
              title={this.state.isEdit ? "Terima Barang" : "Terima Barang"}
              isOpen={this.props.hideModal}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
          {/* End Tambah Terima Barang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(TerimaBarang);
